@media only screen and (max-width: 600px){.main__description--title{font-size:30px}.footer{-webkit-box-align:start;align-items:flex-start;-webkit-box-orient:vertical;-webkit-box-direction:normal;flex-direction:column}}.register{background-color:#eaeaea;display:-webkit-box;display:flex;-webkit-box-align:center;align-items:center;-webkit-box-orient:vertical;-webkit-box-direction:normal;flex-direction:column;-webkit-box-pack:center;justify-content:center;padding:0px 30px;min-height:calc(100vh - 200px)}.register__container{color:#335166;display:-webkit-box;display:flex;-webkit-box-orient:vertical;-webkit-box-direction:normal;flex-direction:column;justify-content:space-around;min-height:500px;padding:0 68px 0;width:450px}.register__container--form{display:-webkit-box;display:flex;-webkit-box-orient:vertical;-webkit-box-direction:normal;flex-direction:column}.register__container--form>input{background-color:rgba(255,255,255,0.1);border:2px solid #335166;border-radius:25px;color:#335166;font-family:'Muli', sans-serif;font-size:16px;height:50px;margin-bottom:20px;outline:none;padding:0 20px}.register__container a{color:#335166;font-size:16px;font-weight:500;text-align:center;text-decoration:none}.register__container--form .button{background-color:#335166;border:none;border-radius:25px;color:white;cursor:pointer;font-size:16px;font-weight:bold;font-family:'Muli', sans-serif;height:50px;letter-spacing:1px;margin:10px 0}.register__container a:hover{text-decoration:underline}
