@media only screen and (max-width: 600px){.main__description--title{font-size:30px}.footer{-webkit-box-align:start;align-items:flex-start;-webkit-box-orient:vertical;-webkit-box-direction:normal;flex-direction:column}}section{color:#335166;padding:0 18px;height:100%}.origin,.destination{background-color:rgba(255,255,255,0.1);border:2px solid #335166;border-radius:25px;color:#335166;font-family:'Muli', sans-serif;font-size:16px;height:50px;margin-bottom:20px;outline:none;padding:0 20px;width:96%}.container-trip-information{display:-webkit-box;display:flex;justify-items:center;width:100%}.container-trip-information>div{margin-right:10px;width:calc(50% - 5px)}.container-trip-information>div>label{display:block;margin:0 20px}.container-trip-information>div>input{box-sizing:border-box;width:100%}.container-buttons{display:-webkit-box;display:flex;justify-items:center;flex-wrap:wrap;width:100%}.container-buttons>div{width:50%}.container-buttons>div>button{width:99%}.search{background-color:#335166;border:none;border-radius:25px;color:white;cursor:pointer;font-size:16px;font-weight:bold;font-family:'Muli', sans-serif;height:50px;letter-spacing:1px;padding:0 20px;margin:10px 0;width:20%}.price{background-color:rgba(255,255,255,0.1);border:2px solid #335166;border-radius:25px;color:#335166;font-family:'Muli', sans-serif;font-size:16px;height:50px;margin-bottom:20px;outline:none;padding:0 20px;width:20%}.duration{background-color:rgba(255,255,255,0.1);border:2px solid #335166;border-radius:25px;color:#335166;font-family:'Muli', sans-serif;font-size:16px;height:50px;margin-bottom:20px;outline:none;padding:0 20px;width:20%}.container-driver-card{border:2px solid;border-radius:40px;display:-webkit-box;display:flex;margin:20px 0;-webkit-box-pack:justify;justify-content:space-between;padding:15px}.card{background-color:white;padding:15px;border-radius:5px;min-height:300px}.img{border-radius:50%}.row{display:-webkit-box;display:flex;flex-wrap:wrap}.details-container{-webkit-box-flex:2;flex:2;width:500px}.avatar-container{flex-basis:250px}
