@media only screen and (max-width: 600px){.main__description--title{font-size:30px}.footer{-webkit-box-align:start;align-items:flex-start;-webkit-box-orient:vertical;-webkit-box-direction:normal;flex-direction:column}}.header{-webkit-box-align:center;align-items:center;background:#0da774;color:white;display:-webkit-box;display:flex;height:60px;-webkit-box-pack:justify;justify-content:space-between;top:0;width:100%}.header a{text-decoration:none}.header__text{font-size:35px;margin-left:15px;font-family:Zhi Mang Xing, cursive;color:white;display:unset;top:-14px;position:relative}.header__img{margin-left:30px;margin-top:16px;width:50px}.header__menu{display:-webkit-box;display:flex;justify-content:space-around;margin-right:30px}.header__menu ul{display:none;list-style:none;margin:0px 0px 0px -14px;padding:0px;position:absolute;width:100px;text-align:right}.header__menu:hover ul,ul:hover{display:block}.header__menu li{margin:10px 0px}.header__menu li a{color:white;text-decoration:none}.header__menu li a:hover{text-decoration:underline}.header__menu--profile{-webkit-box-align:center;align-items:center;display:-webkit-box;display:flex;cursor:pointer;margin:0 15px;color:white}.header__menu--profile a{color:white}.header__menu--profile img{margin-right:8px;width:40px;border-radius:100%;object-fit:cover}@media (max-width: 480px){.header>a>p{display:none}}
